body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f1f1f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div.movie {
  width: 220px;
  min-height: 30vh;
  background: transparent;
  position: relative;
  margin: 10px;
  display: block;
}

div.movie-image {
  height: 80%;
  width: 100%;
  margin: auto;
  -webkit-justify-content: center;
          justify-content: center;
  left: 0;
  position: relative;
}

div.movie-image img {
  box-shadow: 4px 4px 16px 2px #121315;
  height: 100%;
  width: 100%;
  margin: auto;
  border-radius: 3px;
  -webkit-transition: .3s;
  transition: .3s;
}

div.movie-image img:hover {
  -webkit-transform: translateY(-3%);
          transform: translateY(-3%);
}

.download-btn {
  position: absolute;
  top: 80%;
  right: 10%;
  height: 50px;
  width: 50px;
  text-decoration: none;
  background: #e7d10a;
  border-radius: 50%;
  outline: none;
  border: none;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0px;
  cursor: pointer;
  box-shadow: 0px 0px 7px -2px #121315;
}

.download-btn:active {
  -webkit-transform: translateY(3%);
          transform: translateY(3%);
}

div.about {
  width: 100%;
  padding-top: 10px;
  padding-left: 6px;
  height: 20%;
}

div.about p {
  margin: 2px;
  font-family: cursive;
}

p.name {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
}

p.by,
p.pub-by {
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  opacity: 0.5;
}

/* skeleton loader styles */

div.skeleton-movie {
  width: 220px;
  min-height: 30vh;
  background: transparent;
  position: relative;
  margin: 10px;
  display: block;
}

div.skeleton-movie-image {
  height: 310px;
  width: 100%;
  margin: auto;
  -webkit-justify-content: center;
          justify-content: center;
  left: 0;
  position: relative;
  background-color: #635c21;
  box-shadow: 4px 4px 16px 2px #1e2023;
  border-radius: 3px;
  -webkit-transition: .3s;
  transition: .3s;
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
  -webkit-animation-direction: alternate-reverse;
}

.skeleton-download-btn {
  position: absolute;
  top: 80%;
  right: 10%;
  height: 50px;
  width: 50px;
  text-decoration: none;
  background: #6b6320;
  border-radius: 50%;
  outline: none;
  border: none;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0px;
  box-shadow: 0px 0px 7px -2px #121315;
}

div.skeleton-about p {
  border-radius: 5px;
}

p.skeleton-name {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  height: 28px;
  width: 200px;
  background-color: #635c21;
  margin: 0px;
  margin-top: 10px;
  -webkit-transition: .3s;
  transition: .3s;
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
  -webkit-animation-delay: .3s;
          animation-delay: .3s;
  -webkit-animation-direction: alternate-reverse;
}

p.skeleton-by,
p.pub-by {
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  opacity: 0.5;
  width: 150px;
  height: 20px;
  background-color: #635c21;
  margin: 0px;
  margin-top: 10px;
  -webkit-transition: .3s;
  transition: .3s;
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
  -webkit-animation-direction: alternate-reverse;
}


@-webkit-keyframes blink {
  to {
    opacity: .5;
  }
}


@keyframes blink {
  to {
    opacity: .5;
  }
}

body {
  font-family: 'Courier New', Courier, monospace;
  font-style: cursive;
  background-color: #1e2126; 
  padding: 0px;
  margin: 0px;
}

.App {
  width: 100%;
  position: relative;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 50px;
  box-shadow: #000 0px -2px 8px 1px;
  background-color: #1e2126;
  z-index: 999;
}

.header-left, .header-center, .header-right {
  width: 10%;
  height: 100%;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.header-left {
  color: #ffe401;
  font-weight: bold;
  font-size: 20px;
}

.header-right {
  width: 15%;
}

.header-center {
  width: 75%;
}

.form-control {
  width: 100%;
  height: 93%;
  outline: none;
  border: none;
  border-bottom: 2px solid #ffe401;
  background: transparent;
  color: #ffe401;
  font-size: 19px;
  padding-left: 10px;
}

.form-control::-webkit-input-placeholder {
  color: #ffe401;
  opacity: 0.5;
  font-size: 17px;
}

.form-control::-ms-input-placeholder {
  color: #ffe401;
  opacity: 0.5;
  font-size: 17px;
}

.form-control::placeholder {
  color: #ffe401;
  opacity: 0.5;
  font-size: 17px;
}

.search-btn {
  border-radius: 0 6px 6px 0;
  background: #ffe401;
  color: #FFF;
  border: none;
  outline: none;
  padding: 5px 20px;
  cursor: pointer;
  height: 100%;
  width: 100%;
  -webkit-transition: .3s;
  transition: .3s;
}

.search-btn:hover {
  background-color: #e7d10a;
}

div.movies {
  margin: 100px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

div.skeleton-movies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.error {
  display: block;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  width: 100%;
}

.error-text {
  color: #fff;
  font-weight: bold;
  font-size: 20px;
}

.error-retry-btn {
  border: none;
  background-color: #e7d10a;
  color: #1e2126;
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

@media (max-width: 768px) {
  .header-left {
    display: none;
  }

  .header-center {
    width: 80%;
  }

  .header-right {
    width: 20%;
  }
}

.lds-roller {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 32px 32px;
          transform-origin: 32px 32px;
}

.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #fff;
  margin: -3px 0 0 -3px;
}

.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}

.lds-roller div:nth-child(1):after {
  top: 50px;
  left: 50px;
  background: #4dd2ff;
}

.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}

.lds-roller div:nth-child(2):after {
  top: 54px;
  left: 45px;
  background: #4dd2ff;
}

.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}

.lds-roller div:nth-child(3):after {
  top: 57px;
  left: 39px;
  background: #fff;
}

.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}

.lds-roller div:nth-child(4):after {
  top: 58px;
  left: 32px;
  background: #4dd2ff;
}

.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}

.lds-roller div:nth-child(5):after {
  top: 57px;
  left: 25px;
  background: #4dd2ff;
}

.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}

.lds-roller div:nth-child(6):after {
  top: 54px;
  left: 19px;
  background: #fff;
}

.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}

.lds-roller div:nth-child(7):after {
  top: 50px;
  left: 14px;
  background: #4dd2ff;
}

.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}

.lds-roller div:nth-child(8):after {
  top: 45px;
  left: 10px;
  background: #4dd2ff;
}

@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

